.section__padding {
  padding: 4rem 6rem;
}

.flex__center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.app__bg {
  background: url("./assets/bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: repeat;
  background-attachment: fixed;
}

.app__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  min-height: 100vh;
}

.app__wrapper_info {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

.app__wrapper_img {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  margin-left: 2rem;
}

.app__wrapper_img-reverse {
  justify-content: flex-start;
  margin-right: 2rem;
}

.app__wrapper_img img {
  width: 65%;
}

.custom__button {
  background-color: var(--color-mint_button);
  color: var(--color-brown);
  font-family: var(--font-base);
  font-weight: 700;
  letter-spacing: 0.04em;
  line-height: 28px;
  font-size: 20px;
  padding: 0.5rem 1.5rem;
  border-radius: 8px;
  border: none;
  outline: none;
  cursor: pointer;
}

.p_syne_mono {
  font-family: var(--font-base);
  color: var(--color-white);
  font-weight: 700;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  font-feature-settings: "tnum" on, "lnum" on;
  line-height: 29.9px;
  font-size: 23px;
}

.p_inconsolata {
  font-family: var(--font-alt);
  color: var(--color-white);
  font-weight: 400;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  line-height: 28px;
  font-size: 20px;
}

.headtext__syne_mono_mint {
  font-family: var(--font-base);
  color: var(--color-mint);
  font-size: 64px;
  line-height: 83.2px;
  letter-spacing: 0.04em;
  text-transform: capitalize;
}

.headtext__syne_mono_brown {
  font-family: var(--font-base);
  color: var(--color-brown);
  font-size: 64px;
  line-height: 83.2px;
  letter-spacing: 0.04em;
  text-transform: capitalize;
}

@media screen and (min-width: 2000px) {
  .custom__button,
  .p_syne_mono {
    font-size: 37px;
    line-height: 67px;
  }

  .p_inconsolata {
    font-size: 30px;
    line-height: 50px;
  }

  .headtext__syne_mono_mint,
  .headtext__syne_mono_brown {
    font-size: 150px;
    line-height: 210px;
  }
}

@media screen and (max-width: 1150px) {
  .app__bg {
    background: url("./assets/bg.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: repeat;
  }
  .app__wrapper {
    flex-direction: column;
  }

  .app__wrapper_img {
    margin: 5rem 0 0 0;
  }

  .app__wrapper_img-reverse {
    margin: 0 0 5rem 0;
  }

  .app__wrapper_img img {
    width: 100%;
  }
}

@media screen and (max-width: 850px) {
  .section__padding {
    padding: 4rem;
  }
}

@media screen and (max-width: 650px) {
  .section__padding {
    padding: 4rem 2rem;
  }

  .p_syne_mono {
    font-size: 21px;
  }

  .p_inconsolata {
    font-size: 20px;
  }
}

@media screen and (max-width: 450px) {
  .p_inconsolata {
    font-size: 15px;
  }

  .p_syne_mono {
    font-size: 19px;
  }

  .headtext__syne_mono_mint,
  .headtext__syne_mono_brown {
    font-size: 45px;
    line-height: 70px;
  }
}
