.app__price {
    flex-direction: column;
    background: var(--color-brown);
}

.app__price-title {
    margin-bottom: 0.5rem;
    text-align: center;
}

.app__price-list {
    width: 100%;
    margin: 1rem 0;

    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
}

.app__price-heading {
    font-family: var(--font-base);
    font-weight: 600;
    font-size: 45px;
    line-height: 58.5px;
    letter-spacing: 0.04em;
    color: var(--color-white);
}

.app__price-list_item {
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__price-list_img {
    width: 400px;
    margin: 0 2rem;
}

.app__price-list_img img {
    width: 100%;
    height: auto;
}

.app__price_list_items {
    display: flex;
    flex-direction: column;

    margin: 2rem 0;
    width: 100%;
}

@media screen and (min-width: 2000px) {
    .app__price-list_img {
        width: 650px;
    }

    .app__price-list_img img {
        height: 920px;
    }
}

@media screen and (max-width: 1150px) {
    .app__price-list {
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .app__price-list_img {
        margin: 3rem 0;
        width: 650px;
    }
}

@media screen and (max-width: 650px) {
    .app__price-list_img {
        width: 100%;
    }

    .app__price-heading {
        font-size: 35px;
        line-height: 48.5px;
    }
}